import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'catalogs',
        children: [
          {
            path: '',
            loadChildren: './catalogs/catalogs-page.module#CatalogsPageModule'
          },
          {
            path: 'items',
            loadChildren: './items/items.module#ItemsPageModule'
          },
          {
            path: 'editItem',
            loadChildren: './edit-item/edit-item-page.module#EditItemPageModule'
          },
          {
            path: 'newCatalog',
            loadChildren: './new-catalog/new-catalog-page.module#NewCatalogPageModule'
          }
        ]
      },
      {
        path: 'wallet',
        children: [
          {
            path: '',
            loadChildren: './tab2/tab2.module#Tab2PageModule'
          }
        ]
      },
      {
        path: 'profile',
        children: [
          {
            path: '',
            loadChildren: './tab3/tab3.module#Tab3PageModule'
          }
        ]
      }
    ]
  },
  {
    path: '',
    redirectTo: 'tabs/catalogs',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class TabsPageRoutingModule {}
